* {
  margin: 0;
  padding: 0;
}

nav {
  background: rgb(169, 169, 169);
  color: white;
}

main {
  /* width: 84rem; */
  background: rgb(169, 169, 169);
  aspect-ratio: auto;
  display: flex;
  flex-direction: column;
}
#head {
  font-weight: bolder;
  font-style: italic;
}
.png {
  margin: 20px;
  width: 104px;
  height: 104px;
}
/* .card-contianer {
  width: 114rem;
} */
.col {
  /* margin-left: 32px; */
  /* width: 29vw; */
  aspect-ratio: auto;
  display: flex;
  flex-direction: column;
  width: 33%;
}

.dark {
  background: black;
  color: white;
}

.day-mode {
  width: 37px;
  height: 35px;
  background: url("../src/images/dark-mode.png") no-repeat center center/cover;
  border: 2px solid #6c757d;
}
.night-mode {
  width: 37px;
  height: 35px;
  background: url("../src/images/sun.png") no-repeat center center/cover;
  border: 2px solid #212529;
}
.header {
  border-radius: 6px;
  border: 1px solid white;
  height: 64px;
}
.cbody {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 6px;
  padding: 10px;
  height: 25rem;
}
#logo {
  width: 90px;
  height: 90px;
}
a {
  color: white;
}
.text {
  font-size: 30px;
  color: white;
  text-decoration: none;
  font-weight: bolder;
}
.text2 {
  margin: 12px;
  color: white;
  text-decoration: none;
  position: relative;
  right: -3px;
  bottom: -5px;
}
.tc {
  padding: 16px 36px;
  color: white;
  width: 12%;
  box-sizing: border-box;
}
.change {
  color: white;
}
.color {
  color: black;
}
/* thead {
  width: 10%;
  box-sizing: border-box;
} */
.row {
  margin: auto;
  width: 80%;
  display: flex;
}
.table-responsive {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin: auto;
  display: block;
  width: 100%;
  aspect-ratio: auto;
}
.table-container-second{
width: 100%;
}
/* responsiveness establishment */
@media screen and (max-width: 600px) {
  main {
    aspect-ratio: auto;
    display: flex;
    flex-direction: column;
    margin: auto;
  }
  .row {
    display: flex;
    /* flex-direction: row; */
    aspect-ratio: 19/9;
    padding: 1;
    width: 94%;
    /* flex-grow: 1; */
    flex-wrap: wrap;
    margin: auto;
  }
  .col {
    /* margin-left: 32px; */
    /* width: 29vw; */
    aspect-ratio: auto;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    /* margin: -10px; */
    width: 52%;
  }
  #col1 {
    margin-left: 0;
  }
}
